import React, { useState, useEffect}from 'react';
import {BrowserRouter as Router, useHistory} from 'react-router-dom';
import {
  Row,
  Card,
  CardBody,
  Nav,
  NavItem,
  NavLink,
  Button,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownItem,
  DropdownMenu,
  TabContent,
  TabPane,
  Badge,
  CardTitle,
  CardSubtitle,
  CardText,
  CardImg,
} from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import productData from '../../../data/products';
import posts from '../../../data/posts';
import hacks from '../../../data/hacks';
import RecentHack from '../../../components/common/RecentHack';
import Popups from '../../../components/Popups/Popups';
import axios from 'axios';
import { apiUri } from '../../../constants/defaultValues';

const products = productData.slice(0, 15);
const Challenge = ({ match }) => {
  const [appState, setAppState] = useState(true);
  const [hackData, SetChallengeList] = useState([])
  useEffect(() => {
    hackList()
  }, []);

  const hackList = () => {
    const userId = JSON.parse(window.localStorage.getItem('sn_user'));
    const data = {
      organizer_id: userId.id
    }
    axios.post(apiUri+'/getHackathonList', data).then((response) => {
      const dataItem = response.data.data;
      setAppState(false);
      SetChallengeList(dataItem)
    });
  }
  
  const history=useHistory();
  const handleHistory=()=>{
      history.push("/Layout1");
  }

console.log(hackData)
  return (
    <>
      <Row>
        <Colxx xxs="8">
          <Breadcrumb heading="menu.challenges" match={match} />
          </Colxx>
          <Colxx xxs="4" style={{justifyContent: 'flex-end', alignItem:'right'}}>
            <Router>
             {/* <Button color="primary" onClick={handleHistory}>Add Hackathon</Button> */}
             <Popups actionSet={hackList}/>
             </Router>
          </Colxx> 
        
      </Row>
      <Separator className="mb-5" />
      {appState != true ?
      <Row>
      {hackData.length != 0 && hackData.map((hack) => {
        return (
          <Colxx xxs="12" lg="3" className="mb-4" key={hack.id}>
          <RecentHack data={hack} />
          </Colxx>
          );
        })}
      </Row> 
      : ""
      }
    </>
  );
};

export default Challenge;
