import React from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  Row,
} from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { Separator } from './CustomBootstrap';

const getBadgeColor = (status) => {
  if (status === 1) return 'outline-success';
  if (status === 0) return 'outline-warning';
  if (status === 2) return 'outline-danger';
  return 'primary';
};

const RecentHack = ({ data }) => {
  const { name, status, createDate } = data;
  return (
    <Card>
      <CardBody>
        <img
          src={data.hackathon_cover_img || '/assets/img/products/fat-rascal-thumb.jpg'}
          alt="thumbnail"
          style={{width: '100%', height: '180px'}}
        />
        <CardTitle>
          <p className="lead" style={{marginTop: '10px'}}>{name}</p>
        </CardTitle>
        {/* <CardSubtitle>
          <p style={{fontSize:'10px', marginTop: '-10px'}}>{data.about}</p>
        </CardSubtitle> */}
        <Badge color={getBadgeColor(status)} pill outline className="mb-3">
          {status == 0 && "Under Review"}
          {status == 1 && "Active"}
          {status == 2 && "Decline"}
        </Badge>
      </CardBody>
    </Card>
  );
};

export default RecentHack;
